.main {
	padding: 100px;
	background-color: rgb(255, 0, 119);
	text-align: center;
	
	& h1 {
		margin: 30px 0;
		text-transform: uppercase;
	}

	.logo {
		margin: 30px 0;
		flex-shrink: 0;
	}

	.content {
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: center;
	}

	.contact-us, button {
		flex-shrink: 0;
		width: 100%;
		margin: 30px 0;
		align-self: flex-start;
		color: #000;
		font-size: 24px;
		line-height: 35px;
		background-color: rgba(255, 255, 255, 0.784);
		padding: 10px 20px;
		border-radius: 15px 0 15px 15px;
		cursor: pointer;
		transition: background-color .3s, color .3s;
		user-select: none;
		&.disabled {
			pointer-events: none;
			opacity: 0.2;
		}

		&:hover {
			background-color: #E7B656;
			color: #ffffff;
		}
	}

	.form-group {
		display: flex;
		align-items: center;
		gap: 30px;
		font-size: 30px;
		margin-bottom: 20px;
	}
	
	.radio-group {
		margin-top: 10px;
	}
	
	.radio-group input[type="radio"] {
		width: 30px;
		height: 30px;
		margin-right: 10px;
	}
}